.about-div{
width: 100%;
min-height: 25vh;
display: flex;
flex-direction: column;
background: rgb(0,146,172);
background: linear-gradient(90deg, rgba(0,146,172,1) 0%, rgba(0,212,255,1) 100%);

}
.about-title{
  display: flex;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  font-size: calc(16px + 1.5vw);
  color: white;
  font-weight: bold;
}

.about-info-div{
display: flex;
flex-direction: column;
width: 50%;
margin-left: auto;
margin-right: auto;
}
.about-info{
color: white;
font-weight: bold;
justify-content: left;
}
.info-div{
  width: 50%;
  margin-left: auto;
  margin-right: auto;
  justify-content: left;
}
.about-button-div{
  width: 50%;
  margin-left: auto;
  margin-right: auto;
  justify-content: left;
}
.about-button{
  display: flex;
  font-size: calc(16px + 0.1vw);
  color: rgb(255, 255, 255);
  background-color: transparent;
  border-color: #ffffff;
  width: auto;
  margin-top: 8px;
  letter-spacing: 1px;
  cursor: pointer;
  box-shadow: inset 0 0 0 0 #ffffff;
  -webkit-transition: ease-out 0.4s;
  -moz-transition: ease-out 0.4s;
  transition: ease-out 0.4s;
}

.about-button:hover{
  box-shadow: inset 400px 0 0 0 #ffffff;
  color: rgb(0, 146, 172);
}